import { NgModule, Optional, SkipSelf } from '@angular/core';
import { CommonModule } from '@angular/common';
import { HTTP_INTERCEPTORS } from '@angular/common/http';

import { AuthGuard, AuthService, LoggedInGuard, LoaderService, LoadingInterceptor, ErrorHandlerInterceptor, TokenInterceptor, AlertService } from './services';
import { ProductCartService } from '../modules/products/services/ProductCart.service';
import { FavouriteService } from '../modules/products/services/favourite.service';
import { PermissionGuard } from './services/guards/auth/permission.guard';
/**
 * @description this module contain all singletons services
 * It is only imported once on the app module
 */
@NgModule({
  declarations: [],
  imports: [
    CommonModule
  ],
  providers: [
    AuthGuard,
    PermissionGuard,
    AuthService,
    LoggedInGuard,
    AlertService,
    LoaderService,
    ProductCartService,
    FavouriteService,
    { provide: HTTP_INTERCEPTORS, useClass: TokenInterceptor, multi: true, },
    { provide: HTTP_INTERCEPTORS, useClass: LoadingInterceptor, multi: true },
    { provide: HTTP_INTERCEPTORS, useClass: ErrorHandlerInterceptor, multi: true },

  ],
})
export class CoreModule {
  /**
 * To make core module a real singleton module and prevent importing it more than one time
 * @param {CoreModule} parentModule
 * @memberof CoreModule
 */
  constructor(@Optional() @SkipSelf() parentModule: CoreModule) {

    this.throwIfAlreadyLoaded(parentModule, `CoreModule`)
    //@Optional : a constructor paramater decorator that makes a dependency as optional
    //@SkipSelf : go to the parent injector and see if we can import a core module
  }
  throwIfAlreadyLoaded(parentModule: any, moduleName: string) {
    if (parentModule) { // has parent module
      throw new Error(`${moduleName} has already been loaded. Import core module in the app module only.`)
    }
  }
}
