import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { AuthService } from 'src/app/core/services';
import { UserData } from 'src/app/modules/auth/models';
import { Type } from 'src/app/modules/products/components/favourites/favourites.component';
import { CartItem } from 'src/app/modules/products/models/cart-item.model';
import { FavouriteService } from 'src/app/modules/products/services/favourite.service';
import { ProductCartService } from 'src/app/modules/products/services/ProductCart.service';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss'],
})
export class HeaderComponent implements OnInit {
  userData: UserData;
  productsTotalPrice: number = 0;
  constructor(
    private authSrvc: AuthService,
    public ProductCartService: ProductCartService,
    public FavouriteService: FavouriteService,
    private router: Router,
    public translate: TranslateService
  ) { }

  ngOnInit(): void {
    this.authSrvc.userData$.subscribe((data) => {
      if (data) this.userData = data;
    });
    this.getRole();
    this.getIsVerified()
    this.getBusinessStatus()
  }

  public get cartProduct(): CartItem[] {
    this.productsTotalPrice = this.ProductCartService.productsTotalPrice
    return this.ProductCartService.productArr
  }


  logout() {
    this.authSrvc.logout();
  }

  role: string = ""
  getRole() {
    this.role = this.authSrvc.role
  }
  IsVerified: boolean
  getIsVerified() {
    this.IsVerified = this.authSrvc.isVerified
  }
  // businessStatus: "approved"
  businessStatus: string = ""
  getBusinessStatus() {
    this.businessStatus = this.authSrvc.businessStatus
  }

  goTo(item) {
    this.router.navigate([`products/${Type[item['categoryName']]}/${item['versionId']}`])
  }

  switchLang(lang: string) {
    localStorage.setItem("lang", lang)
    this.translate.use(lang);
  }
}
