import { Injectable, OnDestroy } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, UrlTree, Router } from '@angular/router';
import { Observable, Subscription } from 'rxjs';
import { AuthService } from '../../auth/auth.service';
/**
 * this guard is to limit pages that used to from logged in users only
 */
@Injectable()
export class PermissionGuard implements CanActivate {
  constructor(
    private authSrvc: AuthService,
    private router: Router
  ) {
    this.getRole();
    this.getIsVerified()
    this.getBusinessStatus()
  }
  canActivate(
    next: ActivatedRouteSnapshot,
    state: RouterStateSnapshot): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {
    if (this.role === 'Business' && this.businessStatus === 'approved') {
      return true
    } else {
      this.router.navigate(['/login'])
      return false
    }
  }

  role: string = ""
  getRole() {
    this.role = this.authSrvc.role
  }
  IsVerified: boolean
  getIsVerified() {
    this.IsVerified = this.authSrvc.isVerified
  }
  businessStatus: string = ""
  getBusinessStatus() {
    this.businessStatus = this.authSrvc.businessStatus
  }
}
