import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ApiResponse } from 'src/app/models/api-response.model';
import { environment as env } from '../../../../environments/environment';
import { AuthService } from 'src/app/core/services';
import { UserData } from 'src/app/modules/auth/models/userData.model';
import { CartItem } from '../models/cart-item.model';
import { CartRequest } from '../models/cart-request.model';
import { map, tap } from 'rxjs/operators';
import { BehaviorSubject } from 'rxjs';
import { OrderAddress, orderOverViewData } from '../components/checkout/order-over-view.model';

@Injectable()
export class ProductCartService {
  userData: UserData
  productArr: CartItem[] = [];
  productsTotalPrice: number = 0;
  constructor(
    private http: HttpClient,
    private authSrvc: AuthService,
  ) {
    this.authSrvc.userData$.subscribe(
      (userData) => {
        this.userData = userData
        if (!this.userData || !this.userData.id) return this.productArr = []
        this.getAllCartItem()
      }
    )
  }


  orderOverViewData: orderOverViewData
  orderOverViewData$: BehaviorSubject<object> = new BehaviorSubject({});


  public get orderOverViewData$value() {
    return this.orderOverViewData$.value
  }

  getAllCartItem() {
    return this.http.get<ApiResponse<CartItem[]>>(`${env.baseUrl}CartItem?FilterSearch[userId]=${this.userData.id}`).pipe(map(res => {
      this.productArr = res.data;
      this.orderOverViewData$.next(Object.assign(this.orderOverViewData$value, { orderItem: res.data }));
      this.getproductsTotalPrice()
    })).subscribe(data => { })
  }

  addToCart(productData) {
    return this.http.post(`${env.baseUrl}CartItem/AddProductToCartItems`, productData)
      .pipe(tap((res) => {
        this.getAllCartItem()
      }))
  }

  deleteFromCart(id) {
    return this.http.delete<ApiResponse<CartItem>>(`${env.baseUrl}CartItem/${id}`).pipe(tap(res => {
      this.getAllCartItem()
    }))
  }

  getproductsTotalPrice() {
    this.productsTotalPrice = 0;
    for (let prod of this.productArr)
      this.productsTotalPrice += prod['productPrice'] * prod.quantity;
  }


  addMobileToCart(productData) {
    return this.http.post(`${env.baseUrl}CartItem/AddMobilePhoneToCartItems`, productData)
      .pipe(tap((res) => {
        this.getAllCartItem()
      }))
  }

  addAccessoryToCart(productData) {
    return this.http.post(`${env.baseUrl}CartItem/AddAccessoryVersionToCartItems`, productData)
      .pipe(tap((res) => {
        this.getAllCartItem()
      }))
  }

  addHardWareToCart(productData) {
    return this.http.post(`${env.baseUrl}CartItem/AddHardwarePartToCartItems`, productData)
      .pipe(tap((res) => {
        this.getAllCartItem()
      }))
  }

  // /api/v{version}/CartItem/{id}
  updateQuantity(data) {
    return this.http.put(`${env.baseUrl}CartItem/${data.id}`, data)
      .pipe(tap((res) => {
        this.getAllCartItem()
      }))
  }

  addOrder(order: any) {
    return this.http.post(`${env.baseUrl}Order`, order)
  }


}
