import { Injectable } from '@angular/core';
import { ToastrService, IndividualConfig } from 'ngx-toastr';

@Injectable()
export class AlertService {
  constructor(
    private toastrSrvc: ToastrService
    ) { }

  private open( message:string, type:ToasterTypes, autoClose:boolean, duration:number ){

    let config : Partial<IndividualConfig> = {}
    if(!autoClose) {
      config.closeButton = true
      config.disableTimeOut = true
    }
    else if ( !isNaN(duration) ) config.timeOut = duration

    this.toastrSrvc[type]( message, type.toUpperCase(), config )
  }

  success(message:string, autoClose:boolean = true, duration:number=1000){
    this.open(message,ToasterTypes.success,autoClose,duration)
  }

  error(message:string,autoClose:boolean = true, duration:number=2000){
    this.open(message,ToasterTypes.error,autoClose,duration)
  }

  info(message:string,autoClose:boolean = true, duration:number=2000){
    this.open(message,ToasterTypes.info,autoClose,duration)
  }

  warning(message:string,autoClose:boolean = true, duration:number=2000){
    this.open(message,ToasterTypes.info,autoClose,duration)
  }
}

export enum ToasterTypes {
  success= "success",
  error= "error",
  info= "info",
  warning= "warning",
}
