import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { BehaviorSubject, forkJoin, Observable } from 'rxjs';
import { tap } from 'rxjs/operators';
import { AuthService } from 'src/app/core/services';
import { ApiResponse } from 'src/app/models/api-response.model';
import { environment as env } from 'src/environments/environment';
import { UserData } from '../../auth/models';
import { FavoriteResponse } from '../models/favorite.response.model';
import { MobilePhone } from '../models/mobile-phone.model';

@Injectable()
export class FavouriteService {
  private favouritesSubj: BehaviorSubject<FavoriteResponse[]> = new BehaviorSubject<FavoriteResponse[]>([])
  favourites$: Observable<FavoriteResponse[]> = this.favouritesSubj.asObservable()
  userData: UserData
  loggedIn: boolean
  favouriteCashedName: string
  constructor(
    private http: HttpClient,
    private userSrvc: AuthService
  ) {
    this.userSrvc.userData$.subscribe((res) => {
      this.userData = res
    })
    this.userSrvc.loggedIn$.subscribe((loggedIn) => {
      this.loggedIn = loggedIn
      if (loggedIn) {
        const cachedFavourites: FavoriteResponse[] = this.getFavouritesFromLocalStorage()
        if (cachedFavourites?.length) {
          forkJoin(cachedFavourites.map((product) => this.addToFavourites(product)))
            .subscribe((res) => {
              localStorage.removeItem(this.favouriteCashedName)
              this.getUserFavourites()
            })
        }
        this.getUserFavourites()
      } else {
        const cachedGuestFavourites: any = localStorage.getItem(this.favouriteCashedName)
        if (cachedGuestFavourites) this.favouritesSubj.next(JSON.parse(cachedGuestFavourites))
      }
    })
  }



  public get favourites$value(): FavoriteResponse[] {
    return this.favouritesSubj.value
  }


  addToFavourites(product?: any) {
    if (!this.loggedIn) {
      this.saveFavouriteItemOnLocalStorage(product)
    }
    //change product.id to version.id
    return this.http.post<ApiResponse<FavoriteResponse>>(`${env.baseUrl}Favourite/AddProductToFavourites`, product)
      .pipe(tap((res) => {
        this.getUserFavourites()
      }))
  }

  removeFromFavourites(id: number) {
    return this.http.delete<ApiResponse<number>>(`${env.baseUrl}Favourite/${id}`)
      .pipe(tap((res) => {
        this.getUserFavourites()
      }))
  }

  getUserFavourites() {
    return this.http.get<ApiResponse<FavoriteResponse[]>>(`${env.baseUrl}Favourite?FilterValue[userId]=${this.userData.id}`)
      .subscribe((res) => {
        this.favouritesSubj.next(res.data)
      })
  }



  getFavouritesFromLocalStorage() {
    const cachedGuestFavourites: any = localStorage.getItem(this.favouriteCashedName)
    if (cachedGuestFavourites)
      return cachedGuestFavourites ? JSON.parse(cachedGuestFavourites) : []
  }
  saveFavouriteItemOnLocalStorage(product: MobilePhone) {
    let favourites: FavoriteResponse[] = this.getFavouritesFromLocalStorage()
    let favouriteItem: FavoriteResponse = new FavoriteResponse()
    // to be completed by adding more info after ungrouped api done and don't forget to create a model for it
    favouriteItem.productName = product.name
    favouriteItem.versionId = product.id
    favouriteItem.cachingId = Math.ceil(Math.random() * 100000)
    favourites.push(favouriteItem)
    localStorage.setItem(this.favouriteCashedName, JSON.stringify(favourites))
  }

  removeFavouriteFromLocalStorage(id: number) {
    let cachedFavourites: FavoriteResponse[] = this.getFavouritesFromLocalStorage()
    cachedFavourites = cachedFavourites.filter(fav => fav.cachingId != id)
    localStorage.setItem(this.favouriteCashedName, JSON.stringify(cachedFavourites))
  }
}
