import { Component, OnInit } from '@angular/core';
import { AuthService } from 'src/app/core/services';
import { UserData } from 'src/app/modules/auth/models/userData.model';

@Component({
  selector: 'app-fix-reqest',
  templateUrl: './fix-reqest.component.html',
  styleUrls: ['./fix-reqest.component.scss']
})
export class FixReqestComponent implements OnInit {

  userData : UserData
  constructor(private authSrvc:AuthService) { }

  ngOnInit(): void {
    this.authSrvc.userData$.subscribe((data)=>{
      if(data) this.userData = data
    })
  }

}
