import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AuthGuard, LoggedInGuard } from './core/services';
import { LayoutComponent } from './layout/layout/layout.component';


const routes: Routes = [
  { path: "", redirectTo: 'products', pathMatch: 'full' },
  {
    path: "",
    loadChildren: () => import('./modules/auth/auth.module').then(m => m.AuthModule),
    canActivateChild: [LoggedInGuard]
  },
  {
    path: '',
    component: LayoutComponent,
    children: [
      {
        path: 'products',
        loadChildren: () => import('./modules/products/products.module').then(m => m.ProductsModule),
      },
      {
        path: 'profile',
        loadChildren: () => import('./modules/profile/profile.module').then(m => m.ProfileModule),
        canActivate: [AuthGuard]
      },
      {
        path: 'fix-request',
        loadChildren: () => import('./modules/fix-request/modules/fix-request.module').then(m => m.FixRequestModule),
      }
    ],

  },
  {
    path: '**',
    redirectTo: '/products/MobilePhone'
  }
];


@NgModule({
  imports: [RouterModule.forRoot(routes, { scrollPositionRestoration: 'top', onSameUrlNavigation: 'reload' })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
